.RecipeContainer {
  display: grid;
  grid-template-columns: repeat(4, 265px);
  grid-gap: 10px;
  margin: auto auto 20px;
  width: 1090px;
  justify-items: center; }

@media screen and (max-width: 1100px) {
  .RecipeContainer {
    grid-template-columns: repeat(3, 265px);
    width: 815px; } }

@media screen and (max-width: 830px) {
  .RecipeContainer {
    grid-template-columns: repeat(2, 265px);
    width: 540px; } }

@media screen and (max-width: 575px) {
  .RecipeContainer {
    grid-template-columns: 265px;
    width: 275px; } }

.RecipeLinkCard--Link {
  text-decoration: none;
  color: black; }

.RecipeLinkCard--div {
  width: 240px;
  height: 275px;
  border-radius: 8px;
  margin: 0;
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12); }
  .RecipeLinkCard--div:hover {
    border: 1px solid #732B23; }
  .RecipeLinkCard--div p,
  .RecipeLinkCard--div h2 {
    margin-left: 3%; }
  .RecipeLinkCard--div h2 {
    font-size: 1.2em; }

.RecipeLinkCard-img {
  width: 94%;
  border-radius: 8px;
  margin: 3%; }

.UserInstructions--div {
  width: 240px;
  height: 350px;
  border-radius: 8px;
  margin: 0;
  padding: 1em;
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12); }
  .UserInstructions--div:hover {
    border: 1px solid #732B23; }
  .UserInstructions--div p,
  .UserInstructions--div h2 {
    margin-left: 3%; }
  .UserInstructions--div h2 {
    font-size: 1.2em; }

.UserInstructions--button {
  background-color: #A55757;
  border-radius: 8px;
  color: #fff;
  display: block;
  font-size: 1em;
  padding: .5em 1.8em;
  margin-top: .6em; }

a {
  text-decoration: none; }

.UserInstructions-img {
  width: 94%;
  border-radius: 8px; }

.RecipeCard--article {
  border-radius: 8px;
  background-color: #fff;
  width: 800px;
  margin: 2em auto;
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12); }

.RecipeCard--p {
  font-weight: bold;
  margin: .5em 0; }

.RecipeCard--div--flex {
  display: flex; }

.RecipeCard--div, .RecipeCard--div-notes {
  display: inline-block;
  margin: 1em 1em 1em 2em; }

.RecipeCard--div-notes {
  width: 400px; }

.RecipeCard--anchor {
  color: #732B23;
  font-size: 1.7em; }
  .RecipeCard--anchor:hover {
    color: blue; }

.RecipeCard--img {
  border-radius: 8px;
  width: 300px; }

.RecipeCard--button {
  background-color: #A55757;
  border-radius: 8px;
  color: #fff;
  display: block;
  font-size: 1em;
  padding: .5em 1.8em;
  margin-top: .6em; }
  .RecipeCard--button:hover {
    background-color: #732B23; }

.RecipeCard--textarea {
  height: 140px;
  width: 400px;
  font-size: 1em; }

.RecipeCard--p--large {
  font-size: 1.2em;
  margin: 1.3em 0 .9em 0; }

.RecipeCard--div--buttons {
  display: flex;
  justify-content: center; }

.RecipeCard--icon--close, .RecipeCard--icon--delete {
  margin-top: .7em;
  margin-left: .5em;
  font-size: 1.2em;
  border: 0; }
  .RecipeCard--icon--close:hover, .RecipeCard--icon--delete:hover {
    color: #732B23; }

.RecipeCard--icon--delete {
  margin-left: auto;
  margin-right: .5em; }

@media screen and (max-width: 900px) {
  .RecipeCard--div--flex {
    flex-direction: column; }
  .RecipeCard--article {
    width: 500px; }
  .RecipeCard--img {
    border-radius: 8px;
    width: 400px; }
  .RecipeCard--div, .RecipeCard--div-notes {
    margin: auto auto 20px; }
  .RecipeCard--div--buttons {
    margin-bottom: 10px; } }

@media screen and (max-width: 550px) {
  .RecipeCard--div--flex {
    flex-direction: column; }
  .RecipeCard--article {
    width: 300px; }
  .RecipeCard--textarea {
    width: 250px; }
  .RecipeCard--img {
    border-radius: 8px;
    width: 250px; }
  .RecipeCard--div-notes {
    width: 250px; } }

.Nav--div {
  background-color: #fff;
  height: 3em;
  display: flex;
  align-items: center;
  margin-bottom: 2em; }

.Nav--NavLink, .Nav--NavLink--margin {
  font-size: 1.5em;
  border-bottom: 2px solid transparent;
  text-decoration: none;
  color: black;
  margin-right: 2em;
  padding: .4em 0; }
  .Nav--NavLink:hover, .Nav--NavLink--margin:hover {
    border-bottom: 2px solid #732B23; }

.Nav--NavLink--margin {
  margin-left: auto; }

.Error404 {
  text-align: center; }

.Error404--img {
  border-radius: 8px;
  display: block;
  margin: auto;
  width: 50%; }

.Error404--p--credit {
  font-size: .8em;
  margin-top: .8em; }

.Loading {
  text-align: center; }

.Loading--img {
  border-radius: 8px;
  display: block;
  margin: auto;
  width: 50%; }

.Loading--p--credit {
  font-size: .8em;
  margin-top: .8em; }

.App--header {
  background-color: #E7DFD3; }

.App--h1 {
  font-family: 'Flamenco', cursive;
  padding-top: 0.7em;
  padding-left: .7em;
  padding-bottom: 0.7em;
  font-size: 3em;
  margin: 0; }

body {
  background-color: #F5F5F6; }

.App--div-attribution {
  margin: 20px, auto;
  width: 275px; }

.App--div-anchor {
  color: blue;
  text-decoration: underline; }
